@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
body {
  margin: 0;
  padding: 0;
}


.footer{
  background:black;
  color: #fff;
  padding: 40px 20px;
}
.btn{
    border-radius: 2px;
}

.btn-green {
    background-color: #0eaaa6 ;
    color: #fff;
}
.btn-green:hover{
    background-color: #068b85;
    color: #fff;
}

.link-green {
    color: #0eaaa6;
}
.link-green:hover{
    color: #068b85;
}
.navbar-dark .navbar-nav .nav-link {
    color: white;
}
.navbar-dark .navbar-toggler {
    color: white;
    border-color: white;
}
@media only screen and (max-width: 768px) {
    .navbar{
        background: #282c34;
    }
}
.modal{
    color: #757575;
}
.home-title{
    font-size: 3.5vw;
}
.home-subtitle{
    font-size: 2.5vw;
}

.bg-transparent{
    background-color: white;

}
@media only screen and (max-width: 768px) {
    .home-title{
        font-size: 4vw;
    }
    .home-subtitle{
        font-size: 3vw;
    }
}
.prop-image:hover {
    opacity: 0.8;
}
.card-post-title{
    font-size: 16px;
    font-weight: bold;
}
.card-post-address{
    font-size: 14px;
}
.card-width{
    width: 20rem;
}


.lg-spinner{
    width: 3rem;
    height: 3rem;
}
.carousel-inner img {
    width: 100%;
    height: 100%;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: black;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

